/*===========BANNER=========*/

.bannerWrapper {
  position: relative;
  z-index: 1;
}

.banner {
  display: block;
  height: 250px;
  width: 100%;
}

/*===========AVATAR=========*/

.photoStatusWrapper {
  display: flex;
  background-color: #19223d;
}

.avatarWrapper {
  margin-top: -85px;
  margin-left: 20px;
  z-index: 99;
  width: 170px;
}

.avatar {
  position: relative;
  border-radius: 50%;
  border: 0.75rem solid #19223d;
  width: 10rem;
  background: #19223d;
}

.avatar img {
  display: block;
  max-width: 100%;
  border-radius: 50%;
}

/*========CHANGE PHOTO BTN===========*/

.changePhoto input {
  position: absolute;
  top: 50%;
  left: 100%;
  display: block;
}

.customFileUpload input[type="file"] {
  display: none;
}

.customFileUpload {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  cursor: pointer;
}

/*===========STATUS=========*/

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #19223d;
  width: 100%;
  padding: 1rem;
}

.headerContainer {
  display: flex;
  margin-bottom: 0.75rem;
}

.headerContainer h4 {
  margin-right: 5px;
}

.wrapper span {
  padding: 5px;
  border: 1px solid transparent;
}

.headerContainer button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding: 0;
  width: 30px;
}

.wrapper p, .wrapper input {
  font-family: 'PoppinsBold', sans-serif;
  font-size: 1.2rem;
  color:#8c91b6;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 20rem;
  padding: 0.33rem 0.2rem;
}

/*===========CONTACT=========*/

.ProfileInfoWrapper {
  background-color: #1e2745;
  margin: 1.75rem;
  padding: 2rem;
  border-radius: 1rem;
}

.item {
  display: flex;
  padding: 0.75rem 0;
}

.item h4 {
  width: 17%;
}

.item p {
  width: 83%;
}

.contactItem {
  display: flex;
  align-items: center;
  padding: 0.33rem;
}

.contactItem a {
  color: #ffff;
  font-family: 'IndieFlower', sans-serif;
  margin-left: 0.75rem;
}

.editBtn {
  display: flex;
  float: right;
  cursor: pointer;
  background-color: transparent;
  border: none;
}

.editBtn [data-tooltip]::after {
  width: 50px;
  left: -60px;
}

.editBtn [data-tooltip]:hover::after {
  left: -70px;
}

/*===========CONTACT EDIT MODE=========*/

.contactItem label {
  width: 13%;
  text-transform: capitalize;
  color: #8c91b6;
}

.mainItem label {
  font-family: 'PoppinsMedium', sans-serif;
  color: #8c91b6;
  display: block;
  margin: 1rem 0;
}

.mainItem textarea {
  width: 50%;
  height: 100px;
}

.ProfileInfoWrapper h3 {
  margin-top: 1rem;
}

/*===========POSTS=========*/

.newPost {
  display: flex;
}