.menu a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  height: 50px;
  width: 50px;
  color: #ccc;
}

.menu {
  background-color: #8c91b6;
  grid-area: menu;
  z-index: 5;
  position: fixed;
  top: 50px;
  height: 100vh;
}

.selected {
  padding: 0;
  box-shadow: 0 0 34px 0 rgba(10, 10, 10, .3);
}

.selected span {
  color: #222;
}