/*=====MAIN======*/

a {
  text-decoration: none;
  color: inherit;
}

a:visited {
  color: inherit;
}

p {
  word-wrap: break-word;
  margin: 0;
  color: #8c91b6;
}

ul {
  padding: 0;
  margin: 0;
}

h3, h4 {
  margin: 0 0 10px;
}

h3 {
  font-family: 'PoppinsBold', sans-serif;
  color: #fd5e3a;
}

h4 {
  color: #8c91b6;
  font-family: 'PoppinsMedium', sans-serif;
}

button:focus {
  outline: none;
}

textarea, input {
  background-color: #222b4b;
  color: #66bfff;
  border: 1px solid #373949;
  padding: .5rem;
  border-radius: 2px;
}

input:focus, textarea:focus {
  outline: none;
  border-color: #8c91b6;
}

textarea::placeholder, input::placeholder {
  font-size: .9rem;
  font-family: 'PoppinsLightItalic';
  color: #b2b3b2;
}

textarea {
  resize: none;
}

textarea::-webkit-scrollbar {
  width: 8px;
}

textarea::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(90, 87, 87, 0.2);
}

textarea::-webkit-scrollbar-thumb {
  background-color: #8c91b6;
  outline: 1px solid #8c91b6;
}

.Menu_selected__3l9o5 .MuiSvgIcon-root {
  color: #fd5e3a;
}

/*=======APP WRAPPER============*/

.app-wrapper {
  display: grid;
  grid-template-areas: "header header" "menu content" "footer footer";
  grid-template-rows: 50px 1fr 50px;
  grid-template-columns: 50px 1fr;
  height: 100vh;
}

.content-wrapper {
  grid-area: content;
  background-color: #222b4b;
}

footer {
  grid-area: footer;
  color: #edf2f6;
  background-color: #131b33;
}

/*===========PAGINATION==========*/

.pagination {
  display: flex;
  align-items: center;
}

.pagination li a {
  font-family: 'PoppinsMedium', sans-serif;
  display: block;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination .active {
  background-color: #fd5e3a;
  color: ivory;
}

.pagination .active {
  font-family: 'PoppinsBold', sans-serif;
}

/*==========TOOLTIPE=========*/

[data-tooltip] {
  position: relative;
}

[data-tooltip]::after {
  line-height: 1;
  font-family: 'PoppinsLightItalic';
  content: attr(data-tooltip);
  position: absolute;
  width: 50px;
  left: 100%;
  top: 0;
  background: #dceaf5;
  color: #777;
  padding: 5px;
  pointer-events: none;
  opacity: 0;
  transition: 1s;
  font-size: 10px;
}

[data-tooltip]:hover::after {
  opacity: 1;
  left: 150%;
}

/*==============BTN=============*/

button {
  font-family: 'PoppinsMedium', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 2rem;
  border: 1px solid transparent;
  text-transform: uppercase;
  font-size: .8rem;
  border-radius: 3px;
  background-color: #66bfff;
  color: #ffff;
  cursor: pointer;
}

button:disabled {
  border: 1px solid #19223d;
  background-color: #19223d;
  color: #525252;
}